import Link from 'next/link'
import Layout from '../components/Layout'

const Index = () => {
    return (
        <div>
            <Layout>
                <section className="slider">
                    <div className="container-fluid">
                        <div className="row">
                            <div className="col-md-6 col-xs-12 col-sm-12">
                                <div className="top-content">
                                    <h3>A citizen engagement tool for africa</h3>
                                    <h1>Govgage helps citizens in<br/> Africa to hold government leaders accountable</h1>
                                    <div className="d-block d-md-none mobile-nav">
                                        <img src="#"/>
                                    </div>
                                    <br/>
                                    <p>
                                        <a href="#">
                                            <button className="btn b-button-lg">Rate your Government Now</button>
                                        </a>
                                    </p>
                                </div>
                            </div>
                            <div className="col-md-6 col-xs-12 col-sm-12 slider-sec"></div>
                        </div>
                    </div>
                </section>
            </Layout>
            <style jsx>{`
            .top-content {
                padding-left: 60px;
                padding-right: 100px;
                padding-top: 150px;
                padding-bottom: 150px;
              }
              
              @media screen and (min-width: 601px) {
                .slider #s0 {
                  background-image: url(/static/images/home-header.png);
                  background-size: cover;
                  background-repeat: no-repeat;
                  padding: 20px;
                }
              }
              
              .slider {
                margin-top: 80px;
                background-image: url(/images/home-header.png);
                background-size: cover;
                background-repeat: no-repeat;
              }
              
              .slider h3 {
                color: #06C2CE;
                font-size: 18px;
                text-transform: uppercase;
              }
              
              .slider h1 {
                font-size: 33px;
                margin-bottom: 30px;
                color: #053E5F;
                text-weight: 600;
              }
              
              .slider button {
                background-color: #06C2CE;
                color: white;
                border: 0px;
                border-radius: 7px;
                padding: 10px;
              }
            `}</style>
        </div>
    );
}

export default Index;